<template>
  <div class="section">
       <table> 
          <tr>
              <!-- <td width="10%">1</td> -->
              <td width="30%">开始搬迁日期</td>
              <td width="30%">{{info.move_at}}</td>
          </tr>
          <tr>
              <!-- <td>&nbsp;</td> -->
              <td>开始安装日期</td>
              <td>{{info.setup_at}}</td>
          </tr>
          <tr>
              <!-- <td>&nbsp;</td> -->
              <td>导眼开钻时间</td>
              <td>{{info.pilot_start_at}}</td>
          </tr>
          <tr>
              <!-- <td>2</td> -->
              <td>侧钻井开工日期</td>
              <td>{{info.sidetracked_start_at}}</td>
          </tr>
          <template v-for="(item,i) in info.drilling_completion_at_list" >
          <tr :key="i">
              <!-- <td rowspan="2">3</td> -->
              <td>第{{i + 1}}次开钻日期</td>
              <td>{{item.drill_at}}</td>
          </tr>
          <tr :key="i+100">
              <td>第{{i + 1}}次完钻日期</td>
              <td>{{item.completion_at}}</td>
          </tr>
          </template>
          <!-- <tr>
              <td>第二次开钻日期</td>
              <td>{{info.drilling_at2}}</td>
          </tr>
          <tr>
              <td>第二次完钻日期</td>
              <td>开钻次数自动向下扩展</td>
          </tr> -->
          <tr>
              <!-- <td>11</td> -->
              <td>完钻日期</td>
              <td>{{info.drilling_finish_at}}</td>
          </tr>
          <tr>
              <!-- <td>12</td> -->
              <td>完井日期</td>
              <td>{{info.completion_finish_at}}</td>
          </tr>
          <tr>
              <!-- <td>13</td> -->
              <td>搬安与准备时间</td>
              <td>{{info.move_prepare_at}}</td>
          </tr>
          <tr>
              <!-- <td>14</td> -->
              <td>钻井时间</td>
              <td>{{info.drilling_period}}</td>
          </tr>
          <tr>
              <!-- <td>15</td> -->
              <td>完井时间</td>
              <td>{{info.completion_period}}</td>
          </tr>
          <tr>
              <!-- <td>16</td> -->
              <td>建井时间</td>
              <td>{{info.setup_period}}</td>
          </tr>
          <tr>
              <!-- <td>17</td> -->
              <td>中途测试时间</td>
              <td>{{info.middle_test_at}}</td>
          </tr>
          <tr>
              <!-- <td rowspan="2">18</td> -->
              <td>完钻井深(m)</td>
              <td>{{info.completion_depth}}</td>
          </tr>
          <tr>
              <td>完钻垂深(m)</td>
              <td>{{info.completion_vertical_depth}}</td>
          </tr>
          <tr>
              <!-- <td rowspan="2">20</td> -->
              <td>油气层顶部井深(m)</td>
              <td>{{info.oil_top_depth}}</td>
          </tr>
          <tr>
              <td>油气层底部井深(m)</td>
              <td>{{info.oil_bottom_depth}}</td>
          </tr>
          <tr>
              <!-- <td>21</td> -->
              <td>完井方法</td>
              <td>{{info.completion_method}}</td>
          </tr>
          <tr>
              <!-- <td>22</td> -->
              <td>钻井台月(月)</td>
              <td>{{info.drilling_num_month}}</td>
          </tr>
          <tr>
              <!-- <td>23</td> -->
              <td>钻井月速(m/月)</td>
              <td>{{info.drilling_speed_month}}</td>
          </tr>
          <tr>
              <!-- <td>24</td> -->
              <td>平均机械钻速(m/h)</td>
              <td>{{info.avg_penetration}}</td>
          </tr>
          <tr>
              <!-- <td>25</td> -->
              <td>井身质量</td>
              <td>{{info.well_quality}}</td>
          </tr>
          <tr>
              <!-- <td>26</td> -->
              <td>固井质量</td>
              <td>{{info.cementation_quality}}</td>
          </tr>
          <tr>
              <!-- <td rowspan="2">27</td> -->
              <td>钻井取心进尺(m)</td>
              <td>{{info.core_footage}}</td>
          </tr>
          <tr>
              <td>平均收获率(%)</td>
              <td>{{info.avg_collection}}</td>
          </tr>
          <tr>
              <!-- <td rowspan="3">28</td> -->
              <td>工程报废进尺(m)</td>
              <td>{{info.fail_footage}}</td>
          </tr>
          <tr>
              <td>返工进尺(m)</td>
              <td>{{info.remend_footage}}</td>
          </tr>
          <tr>
              <td>进尺损失率(%)</td>
              <td>{{info.footage_loss}}</td>
          </tr>
          <tr>
              <!-- <td>29</td> -->
              <td>完井总进尺(m)</td>
              <td>{{info.completion_total_footage}}</td>
          </tr>
          <tr>
              <!-- <td>30</td> -->
              <td>钻井事故故障次数(次)</td>
              <td>{{info.accident_num}}</td>
          </tr>
      </table>
  </div>
</template>

<script>
import request from '@/utils/request'
export default {
  props: ["route"],
  data () {
      return {
         info: {}
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    let data = this.route;
    request({
      url: '/data/query/wellcompletiondata',
      method: 'post',
      data
    }).then(res => {
      this.info = res[0];
    })
  },

  methods: {
  
  }
}
</script>




